.Form {
  /* min-height: 100vh; */
  padding: 143px 40px 20px;
  background: url(../../img/light_desk.svg) center top no-repeat, url(../../img/back_desk.svg) center 80px / 100% 100% no-repeat, #ECF3FB;
  font: 14px/23px Inter;
  color: #7D7E8D;
  text-align: center;
}

.Form .Card {
  position: relative;
  width: 100%;
  margin: 0 auto 20px;
}

.form-footer {
  margin: 1rem auto 0;
  max-width: 70%;
}

.Form .Card .prezent {
  top: -133px;
}

.Form .Card .Headline {
  margin-top: 25px;
}

.Form .Card .text {
  margin: 0;
}

@media (max-width: 797px) {

  .Form {
    position: relative;
    padding: 0;
    height: 100vh;
    background: url(../../img/light_desk.svg) center bottom no-repeat, url(../../img/back_desk.svg) center 80px / 100% 100% no-repeat, #ECF3FB;
    background-size: cover, contain, contain;
    display: grid;
    place-items: center;
  }

  .card-wrapper {
    width: 90%;
    margin: auto;
  }

  .Form .Card {
    width: 100%;
    padding: 1rem 1rem 3rem;
  }

  .Form .Card .prezent {
    top: -7rem;
  }

  .form-footer {
    position: absolute;
    bottom: 30px;
    left: 0;
    right: 0;
    margin: auto;
  }

}